// GNB   ////
import { gnbData } from "../../js/data/gnb";

import { memo } from "react";

//   ///
import $ from 'jquery';

//   
import { TotalMenu } from "../modules/TotalMenu";

// React.memo()    !
export const TopArea = memo(({ catName }) => {
  console.log(" !!!");

  /// GNB   
  const makeList = (dataName) => {
    console.log(":", dataName);
    return gnbData[dataName].map((v, i) => (
      <li key={i}>
        <a href={"#c" + (i + 1)}>{v}</a>
      </li>
    ));
  }; ///////// makeList /////////

  ///   ////////
  return (
    <>
      <div id="top-area">
        <header className="top-area ibx">
          <h1 id="logo">
            <a href="#">
              <img src={process.env.PUBLIC_URL + "/images/main_logo.png"} alt="" />
            </a>
          </h1>
          <nav className="gnb">
            <ul>
              <li className="bld"> li </li>
              {makeList(catName)}
            </ul>
          </nav>
          <div className="ham"
            onClick={(e)=>{
              // toggle() show()/hide() !

              // 1. /
              $('.mbox').toggle();
              // 2.   /
              $(e.currentTarget).toggleClass('on');
              // 3.  / : 
              // -   get(0) !
              let bgm = $('.bgm').get(0);
              if (bgm.paused) { // (1)  
                bgm.play();
              } else { // (2)   
                bgm.pause();
              }
              // console.log(' :',bgm.paused);
            }}
          >
            <span></span> <span></span> <span></span>
          </div>
          {/*   */}
          <TotalMenu />
        </header>
      </div>
    </>
  );
}); //////////// TopArea  ///////////
