///   : Layout.jsx /////

import { useCallback, useRef, useState } from "react";
//  API 
import { pCon } from "../modules/pCon";

import { FooterArea } from "./FooterArea";
import MainArea from "./MainArea";
import { TopArea } from "./TopArea";
import { useNavigate } from "react-router-dom";
import { CartList } from "../modules/CartList";

export default function Layout() {
  // [     ] //////////
  // [1]       
  const flag = useRef(true);
  // ->  true   
  // ->  false      !
  // useRef     
  //         !!!

  // [    ]
  //     'cart'  1
  //  0  !
  // [1]   (true -> 1, false -> 0)
  let stsVal = false;
  // [2]     
  let transVal = null;

  // [    ] ///////////
  //      !
  if (localStorage.getItem("cart")) {
    //   !
    transVal = JSON.parse(localStorage.getItem("cart"));
    //     0   1 
    if (transVal.length !== 0) stsVal = true;
  } ///// if ////////

  console.log("?", stsVal);

  // [     ] //////////
  // [1]  
  const [catName, setCatName] = useState("main");

  // [2]    -    !
  const [transData, setTransData] = useState(transVal);

  // [3]   /////////
  const [csts, setCsts] = useState(stsVal);

  // [     ] //////////

  //      //
  const goNav = useNavigate();

  // [1]  
  const goPage = useCallback((pm1, pm2) => {
    // pm1 -   
    // pm2 - state  
    //   !
    goNav(pm1, pm2);
  }, []);

  /***************************************** 
    [  API   ]
    1. flag -   
    2. setTransData -    
    3. transData -   
    4. setCsts -   
    5. gMode, setGMode 
      -     
    6. gInit -    
  *****************************************/

  ///   ////////
  return (
    // [   API   ] ////
    <pCon.Provider
      value={{
        setCatName, //   setter
        goPage, //  
        flag,
        setTransData,
        transData,
        setCsts,
      }}
    >
      <TopArea catName={catName} />
      <MainArea />
      <FooterArea />
      {/*  */}
      {
        //    csts  true 
        csts &&
        <CartList selData={transData} flag={flag}  />
        //  flag   
        //       !
        // : useRef  .current !
      }
    </pCon.Provider>
  );
} //////////// Layout  ///////////
